<template>
  <main class="content">
    <div class="container-fluid p-0">
      <div class="page-title-right">
        <ol class="breadcrumb m-0">
          <li class="breadcrumb-item">
            <a href="javascript: void(0);">Anasayfa</a>
          </li>
          <li class="breadcrumb-item active">Kullanıcı Ekle</li>
        </ol>
      </div>
      <br />
      <h1 class="h3 mb-3">Kullanıcı Ekle</h1>
      <div class="row">
        <div class="col-12">
          <div class="card">
            <div class="card-body">
              <div class="row">
                <div class="col-xl-6">
                  <div class="form-group">
                    <label>Adı</label>
                    <input
                      type="text"
                      class="form-control"
                      placeholder="Projenin adını giriniz"
                      v-model="isim"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label>Soy Adı</label>
                    <input
                      type="text"
                      class="form-control"
                      placeholder="Projenin adını giriniz"
                      v-model="soyisim"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label>E posta</label>
                    <input
                      type="text"
                      class="form-control"
                      placeholder="E posta adresini giriniz"
                      v-model="email"
                      required
                    />
                  </div>

                  <div class="form-group">
                    <label>Tel</label>
                    <input
                      class="form-control"
                      rows="5"
                      placeholder="Telefon numarasını giriniz"
                      v-model="tel"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <select class="form-control" v-model="yetki">
                      <option value="1">Admin</option>
                      <option value="2">User</option>
                    </select>
                  </div>
                  <br />
                  <br />
                </div>
                <!-- end col-->
              </div>
              <!-- end row -->
              <button class="btn btn-primary" type="button" id="addproject" @click="kaydet">Kaydet</button>
            </div>
            <!-- end card-body -->
          </div>
          <!-- end card-->
        </div>
        <!-- end col-->
      </div>
    </div>
  </main>
</template>

<script>
import Swal from "sweetalert2";
import  state from "../../Helper/custom_state"
export default {
  data() {
    return {
      isim: "",
      soyisim: "",
      email: "",
      tel: "",
      yetki:"1"
    };
  },
  methods: {
    kaydet() {
      if (
        this.email.trim() === "" &&
        this.isim.trim() === "" &&
        this.soyisim.trim() == "" &&
        this.tel.trim() === ""
      ) {
        Swal.fire("", "Tüm alanlar doldurulmalıdır", "error");
      } else {
        this.$http.post(`${state.ip}/api/admin/register`, {
           name: this.isim,
            surname: this.soyisim,
            email: this.email,
            tel: this.tel,
            yetki:this.yetki
        }).then(response => {
            Swal.fire("", response.data.msg, "success");
            (this.isim = ""),
              (this.soyisim = ""),
              (this.email = ""),
              (this.tel = "");
          })
          .catch(function(error) {
            // eslint-disable-next-line no-console
            console.log(error.response);
          });
      }
    }
  }
};
</script>

<style>
</style>